<script>
import $ from 'jquery';
import * as echarts from 'echarts';
//import axios from 'axios';
//import 'echarts4/map/js/province/hunan.js';
import 'echarts/theme/vintage.js';
import 'echarts-wordcloud';
export default {
	mounted(){
	    this.drawPieChart(); 
		this.drawBarChart();
		this.drawTreeChart();
		this.drawScatterChart();
		this.drawMapchart();
		this.drawWordCloud1();
		this.drawWordCloud2();
		this.drawWordCloud3();
		this.drawMulitiRadar()
	    },
	methods: {
		drawPieChart() {
		  // 基于准备好的dom，初始化echarts实例  这个和上面的main对应
		  var myChart = echarts.init(document.getElementById("pie-nest"));
		  // 指定图表的配置项和数据
		  var option = {
		    
		      tooltip: {
		        trigger: 'item',
		        formatter: '{a} <br/>{b}: {c} ({d}%)'
		      },
			  legend: {
			      data: [
			        '省政协',
			        '市州政协',
			        '县市区政协',
			        '委员学习联络委员会',
			        '提案委员会',
			        '经济科技委员会',
			        '农业和农村委员会',
			        '人口资源环境委员会',
			        '文教卫体和文史委员会',
			        '社会和法制委员会',
					'民族和宗教委员会',
					'港澳台侨和外事委员会'
			      ]
			    },
		      series: [
		        {
		          name: '政协委员提交量',
		          type: 'pie',
		          selectedMode: 'single',
		          radius: [0, '30%'],
		          label: {
					formatter: ' {d}%\n{b}',
		            position: 'inner',
		            fontSize: 14,
					color: '#fff',
		          },
		          labelLine: {
		            show: false
		          },
		          data: [
		            { value: 3124, name: '省政协'},
		            { value: 6312, name: '市州政协' },
		            { value: 25592, name: '县市区政协'}
		          ]
		        },
		        {
		          name: '专委会提交量',
		          type: 'pie',
		          radius: ['45%', '60%'],
		          labelLine: {
		            length: 30
		          },
		          label: {
		            formatter: ' {b|{b}：}{c}  {per|{d}%}  ',
		            backgroundColor: '#F6F8FC',
		            borderColor: '#8C8D8E',
		            borderWidth: 1,
		            borderRadius: 4,
		            rich: {
		              a: {
		                color: '#6E7079',
		                lineHeight: 22,
		                align: 'center'
		              },
		              hr: {
		                borderColor: '#8C8D8E',
		                width: '100%',
		                borderWidth: 1,
		                height: 0
		              },
		              b: {
		                color: '#4C5058',
		                fontSize: 14,
		                fontWeight: 'bold',
		                lineHeight: 33
		              },
		              per: {
		                color: '#fff',
		                backgroundColor: '#4C5058',
		                padding: [3, 4],
		                borderRadius: 4
		              }
		            }
		          },
		          data: [
		            { value: 52, name: '委员学习联络委员会' },
		            { value: 136, name: '提案委员会' },
		            { value: 301, name: '经济科技委员会' },
		            { value: 210, name: '农业和农村委员会' },
		            { value: 469, name: '人口资源环境委员会' },
		            { value: 462, name: '文教卫体和文史委员会' },
		            { value: 492, name: '社会和法制委员会' },
		            { value: 423, name: '民族和宗教委员会' },
					{ value: 372, name: '港澳台侨和外事委员会' }
		          ]
		        }
		      ]
		  };
		  // 使用刚指定的配置项和数据显示图表。
		  myChart.setOption(option);
		  window.addEventListener("resize", function() {
		              myChart.resize()
		            })
		},
		drawBarChart() {
			
			// 基于准备好的dom，初始化echarts实例  这个和上面的main对应
			var myChart = echarts.init(document.getElementById("dynamic-bar"));
			var pending = false;
			
			const updateFrequency = 2000;
			const dimension = 0;
			const issueColors = {
			  '城市管理': '#00008b',
			  '交通运输': '#f00',
			  '基层治理': '#ffde00',
			  '教育发展': '#005500',
			  '社会保障': '#003580',
			  '环境保护': '#00ff00',
			  '文化体育': '#000',
			  '文化旅游': '#003897',
			  '法制建设': '#f93',
			  '乡村振兴': '#bc002d',
			  '医药卫生': '#aaffff',
			  '其他': '#000'
			};
			$.when(
			  $.getJSON('data/top10-year.json')
			).done(function (res0) {
			  const data = res0;
			  //console.log(data);
			  const years = [];
			  for (let i = 0; i < data.length; ++i) {
			    if (years.length === 0 || years[years.length - 1] !== data[i][2]) {
			      years.push(data[i][2]);
			    }
			  }
			  //console.log(years);
			  let startIndex = 1;
			  let startYear = years[startIndex];
			  // 指定图表的配置项和数据
			  var option = {
			    grid: {
			      top: 10,
			      bottom: 30,
			      left: 150,
			      right: 80
			    },
			    xAxis: {
			      max: 50,
			      axisLabel: {
			        formatter: function (n) {
			          return n + '%';
			        }
			      }
			    },
			    dataset: {
			      source: data.slice(1).filter(function (d) {
			        return d[2] === startYear;
			      })
			    },
			    yAxis: {
			      type: 'category',
			      inverse: true,
			      max: 10,
			      axisLabel: {
			        show: true,
			        fontSize: 14,
			        formatter: function (value) {
					  //console.log(value);
			          return value;
			        }
			      },
			      animationDuration: 300,
			      animationDurationUpdate: 300
			    },
			    series: [
			      {
			        realtimeSort: true,
			        seriesLayoutBy: 'column',
			        type: 'bar',
			        itemStyle: {
			          color: function (param) {
			            return issueColors[param.value[1]] || '#5470c6';
			          }
			        },
			        encode: {
			          x: dimension,
			          y: 3
			        },
			        label: {
			          show: true,
			          precision: 1,
			          position: 'right',
			          valueAnimation: true,
			          fontFamily: 'monospace'
			        }
			      }
			    ],
			    // Disable init animation.
			    animationDuration: 0,
			    animationDurationUpdate: updateFrequency,
			    animationEasing: 'linear',
			    animationEasingUpdate: 'linear',
			    graphic: {
			      elements: [
			        { 
					  onclick: function () {
						 if (pending) {return;}
						 for (let i = startIndex-1; i < years.length - 1; ++i) {
						    pending = true;
						   (function (i) {
						     setTimeout(function () {
							   if(i>=years.length-2){pending = false};
						       updateYear(years[i+1]);
						     }, (i) * updateFrequency);
						   })(i);
						 }
						 function updateYear(year) {
						   let source = data.slice(1).filter(function (d) {
						     return d[2] === year;
						   });
						   option.series[0].data = source;
						   option.graphic.elements[0].style.text = year;
						   myChart.setOption(option);
						 }
					  },
			          type: 'text',
			          right: 160,
			          bottom: 60,
			          style: {
			            text: startYear,
			            font: 'bolder 80px monospace',
			            fill: 'rgba(100, 100, 100, 0.25)'
			          },
			          z: 100
			        }
			      ]
			    }
			  };
			  // console.log(option);
			  myChart.setOption(option);
			  window.addEventListener("resize", function() {
			              myChart.resize()
			            });

			})
		},
		drawTreeChart() {
			
			// 基于准备好的dom，初始化echarts实例  这个和上面的main对应
			var myChart = echarts.init(document.getElementById("radial-tree"));
			var option;
			
			myChart.showLoading();
			$.when(
			  $.getJSON('data/submit-location.json')
			).done(function (res0) {
			  const treeData = res0;
			  //console.log(treeData)
			  myChart.hideLoading();
