		drawMulitiRadar() {
		  
		  // 基于准备好的dom，初始化echarts实例  这个和上面的main对应
		  var myChart = echarts.init(document.getElementById("multi-radar"));
		  var option;
		  $.when(
		    $.getJSON('data/status-year.json')
		  ).done(function (res0) {
			  const dataRadar=res0;
			  //console.log("data:"+dataRadar);
			  // 指定图表的配置项和数据
			   option = {
			     title: {
			       text: '',
			       subtext: '',
			       top: 10,
			       left: 10
			     },
